<template>
  <!-- 首页部分，中间部分为两部分：上下 两行 -->
  <div class="page-index">
    <el-row>
      <!-- 上：分为两个组件 菜单和生活 -->
      <el-col :span="5">
        <!-- menu -->
        <m-menu />
      </el-col>
      <el-col :span="19">
        <!-- life -->
        <life />
      </el-col>
    </el-row>
    <el-row>
      <!-- 下： -->
      <el-col :span="24">
        <!-- container -->
        <container :nav="iStyle" />
      </el-col>
    </el-row>
    <el-row>
      <!-- 下： -->
      <el-col :span="24">
        <!-- container -->
        <container :nav="recomment" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import MMenu from '@/components/index/menu.vue'
import Life from '@/components/index/life.vue'
import Container from '@/components/index/container.vue'

export default {
  components: {
    MMenu,
    Life,
    Container
  },
  data () {
    return {
      iStyle: {
        class: 'istyle',
        title: '有格调',
        list: [
          {
            text: '全部',
            tab: 'all'
          },
          {
            text: '约会聚餐',
            tab: 'food'
          },
          {
            text: '丽人SPA',
            tab: 'spa'
          },
          {
            text: '电影演出',
            tab: 'movie'
          },
          {
            text: '品质出游',
            tab: 'travel'
          }
        ]
      },
      recomment: {
        class: 'youhui',
        title: '很优惠',
        list: [{
          text: '全部',
          tab: 'all'
        }, {
          text: '美食',
          tab: 'food'
        }, {
          text: '休闲',
          tab: 'xiuxian'
        }]
      }
    }
  }
}
</script>

<style lang='scss'>
@import "@/assets/css/index/index.scss";
</style>



