<template>
  <div class="m-menu">
    <dl class="nav">
      <dt>全部分类</dt>
      <dd
        v-for="(item, index) in menuList"
        :key="index"
        @mouseenter="menuEnter(item)"
        @mouseleave="menuLeave"
        @click="showDetail"
      >
        <i :class="item.type"></i>
        {{ item.name }}
        <span class="arrow"></span>
      </dd>
    </dl>
    <div v-if="curDetail" class="detail" @mouseenter="detailEnter" @mouseleave="detailLeave">
      <template v-for="(item, index) in curDetail.items">
        <h4 :key="index" @click="showDetail">{{ item.title }}</h4>
        <span v-for="(v, i) in item.items" :key="v + '_' + i">{{ v }}</span>
      </template>
    </div>
  </div>
</template>

<script>
import api from '@/api/index'
export default {
  data () {
    return {
      curDetail: null,
      // menuList: [
      //   {
      //     title: "美食",
      //     icon: "food",
      //     children: [
      //       {
      //         title: "美食",
      //         children: [
      //           "代金券",
      //           "甜点饮品",
      //           "火锅自助餐",
      //           "小吃快餐",
      //           "日韩料理",
      //           "西餐",
      //         ],
      //       },
      //     ],
      //   },
      //   {
      //     title: "外卖",
      //     icon: "takeout",
      //     children: [
      //       {
      //         title: "外卖",
      //         children: ["美团外卖"],
      //       },
      //     ],
      //   },
      //   {
      //     title: "酒店",
      //     icon: "hotel",
      //     children: [
      //       {
      //         title: "酒店星级",
      //         children: ["经典型", "舒适/三星", "高档/四星", "豪华/五星"],
      //       },
      //     ],
      //   },
      // ],
      menuList: []
    }
  },
  created () {
    api.getMenuList().then(res => {
      // console.log(res);
      this.menuList = res.data.data
    })
  },
  methods: {
    // 鼠标移入事件 获取当前鼠标指向的源对象 显示标题中的详情部分
    menuEnter (item) {
      // console.log(item);
      this.curDetail = item
    },
    // 鼠标移出菜单栏时让详情部分消失
    menuLeave () {
      let self = this
      this.timer = setTimeout(() => {
        self.curDetail = null
      }, 3000)
    },
    // 鼠标移入详情部分时让详情部分显示 使用定时器
    detailEnter () {
      clearTimeout(this.timer)
    },
    // 鼠标移出详情部分时让详情部分消失
    detailLeave () {
      this.curDetail = null
    },
    showDetail(){
      this.$router.push({name:'productDetail'})
    }
  }
}
</script>

<style>
</style>



