<template>
<!-- 头部和尾部为公共部分 中间部分设为嵌套路由动态展示-->
  <div class="layout-default">
    <el-container>
      <el-header height="">
        <m-header />
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
      <el-footer height="">
        <m-footer />
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import MHeader from '@/components/header/index.vue'
import MFooter from '@/components/footer/index.vue'
export default {
  components: {
    MHeader,
    MFooter
  }
}
</script>

<style lang="scss">
@import '@/assets/css/public/layout.scss';
</style>



