import axios from 'axios'
axios.defaults.baseURL = 'https://api.duyiedu.com/'
axios.interceptors.request.use(function (config) {
  // 在发送请求前做些什么
  // console.log(config);
  config.params = {
    ...config.params,
    appkey: 'hxjhero_1593140158279'
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})
