<template>
  <!-- 搜索部分又被分为两部分：logo和搜索框 两列-->
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <img
          src="//s0.meituan.net/bs/fe-web-meituan/edcae5e/img/logo.png"
          alt="美团"
        />
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <el-input
            v-model="searchWord"
            placeholder="请输入内容"
            @focus="focusInput"
            @blur="blurInput"
            @input="input"
          />
          <el-button type="primary" icon="el-icon-search" />
          <!-- 鼠标移入搜索框 输入框还没有值 聚焦时显示 -->
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
            <dd v-for="(item,index) in hotPalceList" :key="index">
              <router-link :to="{name: 'goods',params:{name:item}}">{{ item }}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="isSearchList">
            <dd v-for="(item,index) in searchWordList" :key="index">
              <router-link to="/s">{{ item }}</router-link>
            </dd>
          </dl>
        </div>
        <p class="suggest">
          <a href="#" v-for="(item,index) in suggestList" :key="index">{{ item }}</a>
        </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      searchWord: '',
      isFocus: false,
      // 以下两个值通过计算得到
      // isHotPlace: false,
      // isSearchList:false,
      // hotPalceList:['京东第一温泉度假村','99旅馆连锁','尚客优快捷酒店','七大连锁酒店'],
      hotPalceList: [],
      // searchWordList:['火锅','火锅自助餐','火锅 重庆'],
      searchWordList: [],
      suggestList: ['京东第一温泉度假村', '99旅馆连锁', '尚客优快捷酒店', '七大连锁酒店']
    }
  },
  created () {
    api.searchHotWords().then(res => {
      // console.log(res);
      this.hotPalceList = res.data.data
    })
  },
  computed: {
    // 聚焦并且输入框无值时显示hotPlace
    isHotPlace: function () {
      return this.isFocus && !this.searchWord
    },
    // 聚焦并且输入框有值时显示searchList
    isSearchList: function () {
      return this.isFocus && this.searchWord
    }
  },
  methods: {
    // 聚焦事件
    focusInput () {
      this.isFocus = true
    },
    // 失焦事件
    blurInput () {
      // 先点击后失焦
      let self = this
      setTimeout(() => {
        self.isFocus = false
      }, 200)
    },
    input () {
      // console.log(this.searchWord);
      let val = this.searchWord
      api.getSearchList().then(res => {
        console.log(res)
        this.searchWordList = res.data.data.list.filter((item) => {
          return item.indexOf(val) > -1// 根据输入的关键词对数据进行过滤
        })
      })
    }
  }
}
</script>

<style>
</style>



